import React from "react";
import ReactGA from 'react-ga4';

const GetInTouch = ({ heading, message, email, appointmentMessage, appointmentUrl }) => {
  ReactGA.send({ hitType: "pageview", page: "getintouch", title: "Get In Touch" });
  return (
    <>
      <h2 className="display-4 pb-3 text-center">{heading}</h2>
      <p className="lead text-center pb-3">
        {message}, <a href={`mailto:${email}`}>{email}</a>. <br />
        {appointmentMessage}
        <a className="btn  btn-link" href={appointmentUrl} target="_blank" rel="noopener noreferrer">
          here.
        </a>
      </p>

    </>
  );
};

export default GetInTouch;
