import React, { useState, useEffect, useCallback } from "react";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import Row from "react-bootstrap/Row";
import ProjectCard from "./ProjectCard";
import axios from "axios";
import ReactGA from 'react-ga4';

const dummyProject = {
  name: null,
  description: null,
  svn_url: null,
  stargazers_count: null,
  languages_url: null,
  pushed_at: null,
};
const API = "https://api.github.com";
// const gitHubQuery = "/repos?sort=updated&direction=desc";
// const specficQuerry = "https://api.github.com/repos/carlospatinos/";

const Project = ({ heading, username, length, specfic }) => {
  ReactGA.send({ hitType: "pageview", page: "projects", title: "Projects" });
  const allReposAPI = `${API}/users/${username}/repos?sort=updated&direction=desc`;
  const specficReposAPI = `${API}/repos/${username}`;
  const dummyProjectsArr = new Array(length + specfic.length).fill(
    dummyProject
  );

  const [projectsArray, setProjectsArray] = useState([]);

  const fetchRepos = useCallback(async () => {
    let repoList = [];
    let cleanRepoNames = [];
    let cleanRepoList = [];
    try {

      if (specfic.length === 0) {
        // getting all repos
        const response = await axios.get(allReposAPI);
        // slicing to the length
        repoList = [...response.data.slice(0, length)];
      } else {
        // adding specified repos
        try {
          for (let repoName of specfic) {
            const response = await axios.get(`${specficReposAPI}/${repoName}`);
            repoList.push(response.data);
          }
        } catch (error) {
          console.error(error.message);
        }
      }

      // setting projectArray
      // TODO: remove the duplication.
      for (let repo of repoList) {
        if (!cleanRepoNames.includes(repo.name)) {
          cleanRepoNames.push(repo.name)
          cleanRepoList.push(repo)
        }
      }
      // console.log(repoList)
      console.log(cleanRepoList)
      setProjectsArray(cleanRepoList);
    } catch (error) {
      console.error(error.message);
    }
  }, [allReposAPI, length, specfic, specficReposAPI]);

  useEffect(() => {
    fetchRepos();
  }, [fetchRepos]);

  return (
    <Jumbotron fluid id="projects" className="bg-light m-0">
      <Container className="">
        <h2 className="display-4 pb-5 text-center">{heading}</h2>
        <Row>
          {projectsArray.length
            ? projectsArray.map((project, index) => (
              <ProjectCard
                key={`project-card-${index}`}
                id={`project-card-${index}`}
                value={project}
              />
            ))
            : dummyProjectsArr.map((project, index) => (
              <ProjectCard
                key={`dummy-${index}`}
                id={`dummy-${index}`}
                value={project}
              />
            ))}
        </Row>
      </Container>
    </Jumbotron>
  );
};

export default Project;
